  }
  .sidebar-top-nav .has-children ul a {
    padding-left: 18px !important;
  }
}

@media only screen and (min-width: 1170px) {
  .has-children > ul {
    width: 100%;
    z-index: 1;
  }
  .has-children ul a {
    padding-left: 18px;
  }
  .has-children.active > ul {
    /* if the item is active, make the subnavigation visible */
    position: relative;
    display: block;
    /* reset style */
    left: 0;
    box-shadow: none;
  }
  .no-touch .site_sidebar .has-children:hover > ul,
  .site_sidebar .has-children.hover > ul {
    /* show subnavigation on hover */
    display: block;
    opacity: 1;
    visibility: visible;
    z-index: 999;
  }
}

.site_sidebar {
  list-style: none !important;
}

.site_sidebar ul li {
  list-style-type: none;
  border-bottom: 1px solid #bfc5ca;
}

.site_sidebar ul li li {
  list-style-type: none;
  border-bottom: none;
}

.site_sidebar ul > li > ul {
  max-height: 400px;
  overflow: hidden;
  overflow-y: auto;
}

.site_sidebar ul > li > ul li {
  border-bottom: 1px solid #bfc5ca;
}

.product-item {
  padding: 15px;
  background: #fff;
  margin-top: 20px;
  position: relative;
  border: 1px solid #eaeaea;
}

.product-item:hover {
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.4);
}

.product-item:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
  font-size: 0;
  line-height: 0;
  background: linear-gradient(rgba(255, 255, 255, 0) 0%, #fff 80%, #fff 100%);
}

.sticker {
  position: absolute;
  top: 0;
  left: 0;
  width: 63px;
  height: 63px;
}

.sticker-new {
  background: none;
  left: auto;
  right: 0;
}

.item-img-wrapper {
  position: relative;
}

.item-img-wrapper div {
  background: rgba(0, 0, 0, 0.3);
  position: absolute;
  left: 0;
  top: 0;
  display: none;
  width: 100%;
  height: 100%;
  text-align: center;
}

.product-item:hover > .item-img-wrapper > div {
  display: block;
}

.item-img-wrapper div .btn {
  padding: 3px 10px;
  color: #fff;
  border: 1px #fff solid;
  margin: -13px 5px 0;
  background: transparent;
  text-transform: uppercase;
  position: relative;
  top: 50%;
  line-height: 1.4;
  font-size: 12px;
}

.product-item .btn:hover {
  background: #3097D1;
  border-color: #c8c8c8;
}

.product-item h3 {
  font-size: 14px;
  font-weight: 300;
  padding-bottom: 4px;
  text-transform: uppercase;
}

.product-item h3 a {
  color: #3e4d5c;
}

.product-item h3 a:hover {
  color: #E02222;
}

.item-price {
  color: #3097D1;
  font-size: 18px;
  float: left;
  padding-top: 1px;
}

.product-item .add2cart {
  float: right;
  color: #a8aeb3;
  border: 1px #ededed solid;
  padding: 3px 6px;
  text-transform: uppercase;
}

.product-item .add2cart:hover {
  color: #fff;
  background: #3097D1;
  border-color: #3097D1;
}
